import React from 'react'

import {items} from "./catalog/db";

import styles from "./ItemPage.css";

export default function ItemPage({match}) {
    const id = match.params.id;
    // console.log(id)
    const item = items.filter((item,i)=> i === +id-1)[0];
    window.scrollTo(0,0);
    // console.log(item)
    return (
        <div className="item-page">
            <div style={{width:"771px",height:"578px",backgroundColor:"#F0F0F0",textAlign:"center",marginRight:"30px"}}>
                <img src={item.img} style={{width:"400px",height:"508px",marginTop:"10px"}} />
            </div>
            <div>
                <h2 style={{fontSize:"26px",fontWeight:"700"}}>{item.clothType} MARTEEZ</h2>
                <p style={{fontSize:"23px",fontWeight:"450"}}>{item.price} BYN</p>
                <div>
                    <button style={{border:"1px solid #29827C",width:"264px",height:"50px",backgroundColor:"#fff",color:"#29827C",fontWeight:"500",fontSize:"18px"}}>
                        Добавить в корзину
                    </button>
                </div>
                <div>
                    <button style={{border:"1px solid #000000",width:"264px",height:"50px",margin:"15px 0 40px 0",backgroundColor:"#fff",fontWeight:"500",fontSize:"18px"}}>
                        Выбрать размер
                    </button>
                </div>
                <h4 style={{fontSize:"22px",fontWeight:"700",borderBottom:"1px solid black",width:"98px",height:"31px"}}>О товаре</h4>
                <ul style={{listStyle:"none",padding:"0",fontSize:"20px",fontWeight:"400"}}>
                    <li>Состав<span style={{marginLeft:"15px",fontWeight:"200",fontSize:"17px"}}>Полиэстер - 65%, Хлопок - 35%</span></li>
                    <li>Узор<span style={{marginLeft:"15px",fontWeight:"200",fontSize:"17px"}}>{item.pattern === "" ? "нет" : item.pattern}</span></li>
                    <li>Страна производства<span style={{marginLeft:"15px",fontWeight:"200",fontSize:"17px"}}>Италия</span></li>
                    <li>Застёжка<span style={{marginLeft:"15px",fontWeight:"200",fontSize:"17px"}}>Пуговицы</span></li>
                    <li>Артикуль<span style={{marginLeft:"15px",fontWeight:"200",fontSize:"17px"}}>RUBASHKABLACK</span></li>
                    <li>Цвет<span style={{marginLeft:"15px",fontWeight:"200",fontSize:"17px"}}>{item.color}</span></li>
                    <li>Сезон <span style={{marginLeft:"15px",fontWeight:"200",fontSize:"17px"}}>Мульти</span></li>
                    <li>Размеры <span style={{marginLeft:"15px",fontWeight:"200",fontSize:"17px"}}>M S L XL </span></li>
                    <li>Рукав <span style={{marginLeft:"15px",fontWeight:"200",fontSize:"17px"}}>Длинный</span></li>
                </ul>
            </div>
        </div>
    )
}
